.App {
  /* font-family: "Roboto Mono", monospace; */
  font-family: "Montserrat", sans-serif;
  /* font-family: "Source Code Pro", monospace; */
  /* font-family: "Source Sans Pro", sans-serif; */
}
.Content {
  margin-top: 0;
  margin-bottom: 0;
  margin-left: 30%;
  margin-right: 10%;
  width: 60%;
  padding: 0;
  color: rgb(209, 203, 203);
  list-style: none;
}

ul {
  list-style-type: none;
  padding-left: 0;
}

.Content > li {
  padding-top: 15vh;
  min-height: 85vh;
}

.Bkg {
  position: fixed;
  enable-background: 0vh;
}

.Tittle,
.SubTittle {
  text-align: center;
}

.Tittle {
  font-size: 6vh;
  margin-top: 0;
  padding-top: 0;
}

.SubTittle {
  font-size: 2.5vh;
  margin-bottom: 0;
  padding-top: 0;
  margin-top: 0;
}

.btn {
  background-color: #0f3460;
  color: rgb(209, 203, 203);
  padding: 0;
  border: 2px solid transparent;
  border-radius: 10px;
  width: 50%;
  height: 8vh;
  margin-left: 25%;
  margin-right: 25%;
  font-family: inherit;
  font-size: large;
  font-weight: bold;
  transition: border-color 300ms;
  cursor: pointer;
  /* clip-path: polygon(10% 0%, 100% 0%, 100% 100%, 0% 100%, 0% 50%); */
}

.btn:hover {
  border-color: white;
}

nav {
  color: white;
  background-color: #16213eab;
  width: 20%;
  position: fixed;
  display: flex;
  flex-direction: column;
  height: 100%;
  min-width: 200px;
}

nav h1,
nav h3 {
  padding: 0;
  margin-left: 2vw;
  margin-bottom: 0;
}

nav h1 {
  font-size: 4vh;
  color: #e94560;
}

nav h3 {
  font-weight: normal;
}

.FancyButton {
  font-family: inherit;
  background-color: rgba(0, 0, 0, 0);
  width: 80%;
  display: flex;
  border-width: 0;
  cursor: pointer;
  margin: auto;
  margin-left: 2vw;
  padding: 0;
  transition: margin 200ms ease-in-out;
  -webkit-transition: margin 200ms ease-in-out;
}
.CloseButton {
  background-color: #16213eab;
  border-radius: 25%;
  padding: 1.5vh;
  margin-left: 50%;
  position: fixed;
  margin-top: 48vh;
  width: 2.5vh;
  height: 4vh;
}
.FancyButton img {
  width: 5vh;
  height: 5vh;
  margin: auto;
  margin-left: 0;
  transition: -webkit-filter 0.1s ease-in-out;
  transition: filter 0.1s ease-in-out;
  transition: filter 0.1s ease-in-out, -webkit-filter 0.1s ease-in-out;
  -webkit-transition: filter 0.1s ease-in-out;
}

/* 
filter: invert(48%) sepia(13%) saturate(3207%) hue-rotate(130deg)
brightness(95%) contrast(80%); */

.FancyButton h2 {
  color: white;
  width: 30%;
  min-width: 100px;
  text-align: left;
  margin: auto;
  margin-left: 0;
  padding-left: 0;
  transition: color 0.1s ease-in-out;
  -webkit-transition: color 0.1s ease-in-out;
}

.FancyButton:hover h2 {
  color: #e94560;
}

.FancyButton:hover img {
  -webkit-filter: invert(50%) sepia(57%) saturate(7449%) hue-rotate(330deg)
    brightness(101%) contrast(83%);
          filter: invert(50%) sepia(57%) saturate(7449%) hue-rotate(330deg)
    brightness(101%) contrast(83%);
}

.FancyButton:hover {
  margin-left: 15%;
}

.Info {
  display: flex;
  flex-direction: row;
}

.Info img {
  width: 40vw;
  height: 30vw;
}

.Info h1 {
  font-size: calc(30px + 4vw);
  padding: 0;
  margin-top: 0;
  margin-bottom: 0;
  color: #e94560;
}

.Info h4 {
  font-style: italic;
  font-weight: normal;
  font-size: calc(13px + 1vw);
  margin-bottom: 0;
  padding: 0;
}

.Info h3 {
  font-weight: normal;
  margin-top: 4vh;
}

.Projects {
  margin: auto;
  margin-top: 2vh;
  display: flex;
  flex-wrap: wrap;
  padding-left: 0;
}

.Projects div {
  background-color: #16213e;
  margin: auto;
  margin-bottom: 1vh;
  padding: 1vh;
  border-radius: 5px;
  display: flex;
  flex-direction: column;
  border: 0.5vh solid transparent;
  cursor: pointer;
  transition: background-color 200ms ease-in-out;
  -webkit-transition: background-color 200ms ease-in-out;
  font-weight: bold;
}

.Projects div:hover {
  background-color: #0f3460;
}

.Projects div img {
  height: 10vh;
  width: 10vh;
  margin: auto;
  border-radius: 1vh;
  margin-top: 2vh;
}

.Header {
  padding: 2vh;
  text-align: center;
  margin-top: 10vh;
}

.Header h1 {
  color: rgb(209, 203, 203);
  margin: auto;
  margin-bottom: 3vh;
  font-size: 5vh;
}

.Header h3 {
  font-weight: normal;
}

.Header .Image {
  width: 50vw;
  height: 50vh;
  border-radius: 5px;
  margin: auto;
}

.Header .Icon {
  height: 15vh;
  width: 15vh;
  margin: auto;
}

.Header div a {
  background-color: #0f3460;
  font-weight: bold;
  border-radius: 5px;
  padding: 1vh;
  text-decoration: none;
  color: white;
  transition: color 200ms ease-in-out;
}

.Header div a:hover {
  color: #e94560;
}

.MediaList {
  display: flex;
  flex-direction: row;
  justify-content: center;
  flex-wrap: wrap;
  margin-top: 5vw;
  margin-bottom: 5vw;
}

.MediaList > li {
  display: flex;
  align-items: center;
  min-width: 10vh;
  background-color: transparent;
  flex-direction: column;
  margin: 1vh;
  border-radius: 5px;
}

.MediaList img {
  width: 15vh;
  height: 15vh;
  margin-left: 2vh;
  margin-right: 2vh;
}

.MediaList img:hover {
  -webkit-filter: invert(50%) sepia(57%) saturate(7449%) hue-rotate(330deg)
    brightness(101%) contrast(83%);
          filter: invert(50%) sepia(57%) saturate(7449%) hue-rotate(330deg)
    brightness(101%) contrast(83%);
}

.MoreInfo p {
  color: #e94560;
  overflow-x: scroll;
}

.selectors {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
  padding-left: 0;
}

.selector,
.selected {
  background-color: #0f3460;
  color: rgb(209, 203, 203);
  margin: 1vh;
  border: 2px solid;
  border-radius: 10px;
  min-width: 30%;
  height: 8vh;
  font-family: inherit;
  font-size: large;
  font-weight: bold;
  transition: border-color 300ms;
  -webkit-transition: border-color 300ms;
  cursor: pointer;
  padding: 0;
}

.selector {
  border-color: transparent;
}

.selected {
  border-color: white;
}

.selector:hover,
.selector:focus {
  border-color: white;
}

.SkillContainer h1 {
  color: #e94560;
}

.techContainer {
  text-align: center;
}

.Technologies {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  justify-content: center;
}

.Technologies > li {
  width: 20vh;
  background-color: #16213e;
  margin: 1vh;
  border-radius: 5px;
  transition: background-color 300ms ease-in-out;
  -webkit-transition: background-color 300ms ease-in-out;
}

.Technologies > li:hover {
  background-color: #0f3460;
}

.Technologies img {
  width: 10vh;
  height: 10vh;
  margin-left: 5vh;
  margin-right: 5vh;
  margin-bottom: 2vh;
}

